.nav-toggle {
  display: none;
}

@media (max-width: 900px) {
  .nav-toggle {
    width: 2.5rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.14);
    height: 2.5rem;
    background: #f8f8ff;
    display: flex;
    border-radius: 50%;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    cursor: pointer;
    z-index: 1000;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  .nav-toggle.active {
    box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.2);
    align-items: center;
    position: fixed;
    right: 5%;
  }
  .nav-toggle.active .nav-toggle-long {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    margin: 0;
  }
  .nav-toggle.active .nav-toggle-short {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    margin: 0;
    width: 60%;
  }
  .nav-toggle > div {
    -webkit-transform: translate(0deg);
            transform: translate(0deg);
    background: #000;
    margin-right: 10px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  .nav-toggle .nav-toggle-long {
    width: 60%;
    height: 1px;
    margin-bottom: 5px;
  }
  .nav-toggle .nav-toggle-short {
    width: 30%;
    height: 1px;
  }
}

